import React, { Component } from 'react';

export class VideoMessage extends Component {

    render() {
        let class_name = "message_video";
        class_name += this.props.is_incoming? " message_content__incoming" : " message_content__outcoming";

        return (
            <iframe className={class_name} src={this.props.path} frameBorder="0" allowFullScreen />
        );
    }
}