import React, { Component} from 'react';

export class TextMessage extends Component {
    render() {
        let class_name = "message_text";
        class_name += this.props.is_incoming? " message_content__incoming" : " message_content__outcoming";

        return (
            <div className={class_name}>
                {this.props.text}
            </div>
        )
    }
}