import React, { Component} from 'react';

export class LinkMessage extends Component {
    render() {
        let class_name = "message_text message";
        class_name += this.props.is_incoming?
            " message__incoming message_content__incoming" :
            " message__outcoming message_content__outcoming";

        class_name += this.props.is_last? " last_thing" : "";

        return (
            <div className={class_name}>
                <div><a className="message_link" href={this.props.link}>{this.props.text}</a></div>
            </div>
        )
    }
}